form {
	animation: form-intro 0.5s ease-out 0.25s backwards;
	flex: 0 1 auto;
	display: flex;
	text-align: center;
	width: 85%;
	max-width: 25rem;
	margin: 2.5rem auto 0;
}

input[type="text"],
input[type="submit"] {
	font-size: 100%;
	height: 2.75rem;
}

input[type="text"] {
	transition: box-shadow 0.3s ease-out;
	background-color: var(--offwhite);
	text-indent: 0.35em;
	width: 100%;
	border-radius: 0;
	border: 2px solid transparent;
	box-shadow: 0 10px 35px rgba(255, 255, 255, 0.3);
	margin-right: 0.375rem;

	&:invalid {
		box-shadow: 0 3px 20px rgba(255, 50, 50, 0.85);
	}

	&:focus,
	&:empty {
		outline: none;
		box-shadow: 0 3px 20px rgba(255, 255, 255, 0.65);
	}
	
	&:disabled {
		background-color: rgba(237, 237, 237, 0.5);
		border-color: rgba(237, 237, 237, 0.5);
		box-shadow: none;
	}
}

input[type="submit"] {
	transition: background-color 0.3s ease, color 0.3s ease;
	max-width: 10em;
	background: transparent;
	color: var(--offwhite);
	padding: 0.375rem 0.75rem;
	border-radius: 0;
	border: 2px solid var(--offwhite);
	outline: none;
	cursor: pointer;

	&:hover {
		background-color: var(--offwhite);
		color: var(--vibe-blue);
	}
}

@keyframes form-intro {
from {
	opacity: 0;
	transform: translateY(50%);
}
30% {
	opacity: 0.8;
}
to {
	opacity: 1;
	transform: translateY(0);
}
}
